/*
**  define the style for the body in the page
*/
body {
	font-family: $text-font;
	font-size: $default-font-size;
	font-weight: normal;
	line-height: $default-line-height;
	color: $color-grey;
	border: 0.1rem solid lighten($color: $color-primary, $amount: 10%);
	border-radius: 1rem;
	margin: 1.5rem;
	@include rwd-df(xs) {
		margin: 1rem;
	}
}

/*
**  define the style for the primary heading in the page  =>heading-primary
*/
.heading-primary {
	font-family: $heading-font;
	width: fit-content;
	margin: 0 auto 6.4rem;
	font-size: 4.4rem;
	font-weight: 900;
	letter-spacing: -0.5px;
	position: relative;
	text-shadow: 0 5px 5px rgba($color-black, 0.2);
	text-align: center;

	@include rwd-df(sm) {
		font-size: 3rem;
	}

	&::before {
		content: '';
		display: inline-block;
		position: absolute;
		top: 100%;
		#{$start}: 50%;
		transform: translateX(-50%);
		width: 25%;
		height: 0.3rem;
		border-radius: 2px;
		background-color: $color-primary;
	}
}

/*
**  define the style for the secondary heading in the page  =>heading-secondary
*/
.heading-secondary {
	font-family: $heading-font;
	font-size: 2.4rem;
	font-weight: 700;
	margin-bottom: 1.6rem;
}

/*
**  define the style for the tertiary heading in the page  =>heading-tertiary
*/
.heading-tertiary {
}

// /*
// **  define the style for the paragraph  in the page
// */
.paragraph {
	font-size: 2rem;
	line-height: 1.7;

	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}
}

/*
**  define the style for the sections  in the page
*/
.sec {
	padding: $section-padding-vertically 0;

	@include rwd-df(md) {
		padding: 4.8rem 0;
	}
}
