.btn {
	display: inline-block;
	font-size: 1.2rem;
	padding: 1rem 1.6rem;
	font-weight: 500;
	border-radius: 0.8rem;
	text-transform: uppercase;

	background-color: $color-primary;
	color: $color-white;

	&:link,
	&:visited {
		background-color: $color-primary;
		color: $color-white;
		transition: all $default-hover-transition;
	}
	&:hover,
	&:active {
		background-color: lighten($color: $color-primary, $amount: 5%);
	}
}

.btn--big {
	font-size: 1.8rem;
	padding: 1.3rem 2.5rem;
}
