.sec--hero {
	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		align-items: center;

		@include rwd-df(md) {
			grid-template-columns: 1fr;
			text-align: center;
		}
	}

	&__info {
		&__title {
			font-size: 6.4rem;
			line-height: 1;
			letter-spacing: -1px;
			margin-bottom: 3.2rem;
			text-shadow: 0 5px 5px rgba($color-black, 0.2);
			font: $heading-font;

			@include rwd-df(lg) {
				font-size: 4.8rem;
				line-height: 1.2;
			}

			@include rwd-df(sm) {
				font-size: 3.6rem;
				line-height: 1.2;
			}
		}
	}

	&__image {
		max-width: 85%;
		justify-self: end;
		border-radius: 1.2rem;
		box-shadow: 0 0 2px 2px rgba($color-black, 0.3);
		// border: 10px solid red;

		@include rwd-df(md) {
			display: none;
		}
	}
}

/*start-style:: section why-we*/
.sec--why-we {
	&__features {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 3rem;

		@include rwd-df(lg) {
			grid-template-columns: repeat(2, 1fr);
			row-gap: 6rem;
		}

		@include rwd-df(sm) {
			grid-template-columns: 1fr;
		}

		&__item {
			&__icon {
				stroke: $color-primary;
				@include squareSize(3.2rem);
				margin-bottom: 2.4rem;
			}
		}
	}
}
/*end-style:: section why-we*/

/*start-style:: section testimonial*/
.sec--testimonial {
	&__content {
		background-color: $color-primary;
		padding: 2.4rem;
		border-radius: 1rem;
		box-shadow: 0 0 5px 3px rgba($color: $color-black, $alpha: 0.3);

		display: grid;
		align-items: center;
		grid-template-columns: 1fr 3fr;
		gap: 5rem;

		@include rwd-df(md) {
			padding: 1.6rem;
			grid-template-columns: 1fr;
			gap: 2rem;
		}
	}

	&__image {
		border-radius: 1rem;
	}

	&__info {
		color: lighten($color: $color-primary, $amount: 65%);

		&__title {
			font-family: $heading-font;
			font-size: 3.2rem;
			margin-bottom: 2.4rem;
			line-height: 1.2;
		}
		&__quote {
			font-style: italic;
			font-size: 1.8rem;
			line-height: 1.7;
			margin-bottom: 1.6rem;
		}

		&__quote-author {
			font-weight: 500;
			font-size: 1.9rem;
		}
	}
}
/*end-style:: section why-we*/

/*start-style:: section Best Selling*/
.sec--best-selling {
	&__cards {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 12rem;
		margin-bottom: 4rem;

		@include rwd-df(lg) {
			gap: 6rem;
		}

		@include rwd-df(md) {
			grid-template-columns: repeat(2, 1fr);
			gap: 6rem;
		}

		@include rwd-df(sm) {
			gap: 3rem;
		}

		@include rwd-df(xs) {
			grid-template-columns: 1fr;
			gap: 6rem;
		}
	}
}
/*end-style:: section Best Selling*/

/*start-style:: section fqa*/
.sec--details {
	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		align-items: start;
		gap: 6rem;

		@include rwd-df(md) {
			grid-template-columns: 1fr;
			// gap: 4rem;
		}
	}

	&__fqa {
	}

	&__specifications {
		overflow-x: auto;
	}
}
/*end-style:: section fqa*/
