table {
	border-spacing: 0;
	border: none;
	// box-shadow: 0 0 20px 20px rgb($color-black, 0.1);
	width: 100%;
	min-width: 50rem;

	th:first-child {
		border-top-left-radius: 2rem; /* curves the top left */
	}

	th:last-child {
		border-top-right-radius: 2rem; /* curves the top left */
	}

	caption {
		text-align: $start;
		// font-size: 2.4rem;
		// font-weight: 600;
		// margin-bottom: 2rem;
	}

	tr {
		&:nth-of-type(even) {
			background-color: #f8f9fa;
		}

		&:nth-of-type(odd) {
			background-color: #e9ecef;
		}

		&:first-of-type {
			background-color: $color-primary;
		}

		&:last-of-type {
			td:first-child {
				border-bottom-left-radius: 2rem; /* curves the top left */
			}

			td:last-child {
				border-bottom-right-radius: 2rem; /* curves the top left */
			}
		}
	}

	th {
		color: $color-white;
		font-family: $heading-font;
		font-weight: 900;
		text-align: $start;
	}

	td {
		font-weight: 500;
		&:first-of-type {
			font-weight: 800;
		}
	}

	th,
	td {
		padding: 1.5rem 1.5rem;
		@include rwd-df(xs) {
			padding: 1rem;
		}
	}
}
