/*
**  MEDIA QUERY MANAGER  Mixins
*/
@import 'rwd';

/*
**   clearfix Mixin
*/
@mixin clearfix {
	&::after {
		content: '';
		display: table;
		clear: both;
	}
}

/*
**   absCenter Mixin
*/
@mixin absCenter {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

/*
**   squareSize Mixin
*/
@mixin squareSize($deminsion) {
	width: $deminsion;
	height: $deminsion;
}
